body {
  background-color: cornflowerblue;
}

.App {
  background-color: #3f3160;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

#todoform input{
  background-color:rgb(27,112,137);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 25px;
  color:aliceblue ;
}

#todoform input::placeholder{
  color: rgba(225,255,255,0.5);
}

#todoform input:focus, #todoform button:focus{
  outline: none;
}

#todoform button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 10;
  background-color: orange;
  cursor: pointer;
  font-weight: bolder;
  
}