.list{
    color: white;
    width: 300px;
    background-color: violet;
    height: 50px;
    margin: 20px auto;
    border-radius: 5px;
}

.list p{
    padding: 15px ;
    position: relative;  /*need clarity*/
}

.list p span{
    position: absolute;  /*need clarity*/
    right: 10px;
    cursor: pointer;
}

.favicons{
    color: yellow;
    margin: 0px;
    font-size: 15px;
}

.list p input{
    background-color: transparent;
    border: 0;
    color: white;
    font-size: 18px;
}

.list p input:focus{
    outline: none;
}